.App {
  width: 100%;
  height: 100vh;
  background: black;
  display: flex;
  justify-content: center;
  align-items: center;

  .modes {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 20%;
    height: auto;
    display: flex;
    flex-direction: row;
    align-content: center;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    z-index: 100;
    .mode {
      color: white;
      margin: 2.5vh;
      margin-bottom: 1vh;
      font-size: 3vh;
      font-family: "Electrolize", sans-serif;
      span {
        margin-right: 7%;
        position: absolute;
        right: 0;
        width: 2vh;
        height: 2vh;
        background: white;
        border-radius: 50%;
      }
    }
    .choices > .mc {
      font-size: 2vh;
      padding-top: 5%;
      margin-left: 5%;
      width: 100%;
    }
    .arrow {
      width: 1.5vh;
      height: 1.5vh;
      border: none;
      border-right: 0.5vh solid white;
      border-bottom: 0.5vh solid white;
      transform: rotateZ(45deg);
    }

    .choices {
      position: absolute;
      left: 0;
      top: 100%;
      transform-origin: bottom;
      width: 110%;
      height: auto;
      border: 1px solid white;
      background: black;
    }
    .circle::after {
      content: "";
      position: absolute;
      right: 25%;
      top: 25%;
      width: 1vh;
      height: 1vh;
      background: black;
      border-radius: 50%;
    }
  }
}
