.converter {
  position: absolute;
  width: 83vh;
  height: 20vh;

  .result {
    position: absolute;
    background: red;
    width: 50%;
    height: 50%;
    bottom: -50%;
    left: 70%;
    transform: translateX(-50%);
  }

  .button_container {
    height: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    .btnbtn {
      &:nth-child(4n + 5) {
        margin-left: 1.75vh;
      }
      &:nth-child(25) {
        margin-left: 0vh;
      }
      .btn {
        width: 3vh;
        height: 5vh;
        border: none;
        margin: 0;
        padding: 0;
        background: none;
        font-size: 2.5vh;
        color: white;
        outline: none;
        &:active {
          background: grey !important;
        }
        &:hover {
          background: rgb(77, 77, 77);
        }
      }
    }
  }

  .marker {
    position: absolute;
    display: flex;
    flex-wrap: wrap;
    justify-content: left;
    width: 100%;
    height: 100%;
    pointer-events: none;
    top: 10%;
    h1 {
      font-size: 3vh;
      &:nth-child(1) {
        margin-right: 39vh;
        margin-left: 1.5vh;
      }
      &:nth-child(2),
      &:nth-child(5) {
        margin-right: 33vh;
      }
      &:nth-child(4) {
        margin-right: 37.8vh;
        margin-left: 1vh;
      }
    }
  }

  #converterInput {
    font-size: 7vh;
    background: none;
    outline: none;
    border: none;
    border-bottom: 0.15vh solid rgba(255, 166, 0, 0.555);
    color: white;
    margin-bottom: 5vh;
    position: absolute;
    top: 52.5%;
    left: 50%;
    transform: translateX(-50%);
    top: 98%;
  }

  .dropdown {
    position: relative;
    top: 41.5%;
    left: 2vh;
    .dropbtn {
      background-color: transparent;
      color: white;
      padding: 2vh;
      font-size: 2.5vh;
      border: 1px solid rgba(255, 166, 0, 0.555);
      border-top: none;
      outline: none;
      cursor: pointer;
    }
    .dropdown-content {
      display: none;
      position: absolute;
      border: 1px solid orange;
      width: 25vh;
      overflow: auto;
      cursor: pointer;
      p {
        padding: 1vh 2.5vh;
        display: block;
        color: white;
      }
    }
    .show {
      display: block;
    }
  }
}
